body {
  font-family: monospace;
  background-color: #000000;
  color: #ffffff;
  padding: 20px;
  overflow: hidden; /* Prevent scrollbar on the body */

}
#console {
  background-color: black;
  color: green;
  font-family: "Courier New", Courier, monospace;
  padding: 10px;
  min-height: 300px; /* Console starts with 300px height */
  height: auto; /* The console expands as content increases */
  max-height: 85vh; /* Console starts with 300px height */
  overflow-y: auto;
  overflow-x:hidden;
  white-space: pre-line; /* Keeps line breaks */
  border: 1px solid #00ff00;
  font-family:"light";
  font-size: 24px;
  width: 95%;


}

#console div {
  margin: 5px 0;
}

input[type="text"] {
  width: 50%;
  padding: 10px;
  font-family: monospace;
}

button {
  padding: 10px;
}

@font-face {
  font-family: 'Light';
  src: local("FFP"),
  url("./et-artilux.regular.ttf") format("truetype");
}